import React from "react"
import { graphql, Link } from "gatsby"
import { Layout } from "../components/Layout"
import { Page } from "../components/Page"

export const query = () => graphql`
  query DQuery {
    allMongodbTakiwaWarehouseDatasetStructures(
      filter: {
        # ownerEntity: { eq: "5bdb6e35bc23ec002341e6f2" }
        datasetAllowDownload: { eq: true }
      }
    ) {
      edges {
        node {
          id
          datasetLabel
          datasetName
        }
      }
    }
  }
`

export default function Home({ data }) {
  const datasets = data.allMongodbTakiwaWarehouseDatasetStructures.edges

  return (
    <Layout>
      <Page>
        <div>
          Datasets ({datasets && datasets.length})
          {datasets.map(({ node }) => {
            return (
              <p key={node.id}>
                <Link to={`/datasets/${node.id}`}>
                  {node.datasetName} - {node.datasetLabel}
                </Link>
              </p>
            )
          })}
        </div>
      </Page>
    </Layout>
  )
}
